import { useCallback } from "react";

import {
  ReactFlow,
  MiniMap,
  Controls,
  useNodesState,
  useEdgesState,
  addEdge,
} from "@xyflow/react";

import "@xyflow/react/dist/style.css";

import styles from "./diagram.module.css";

import { companyDiagram } from "../../utils/constants";
import DiagramComponent from "../diagram-component/diagram-component";

function Diagram() {
  const nodeTypes = {
    diagram_component: DiagramComponent,
  };

  let initialNodes = [];
  let initialEdges = [];

  Object.values(companyDiagram).forEach((value) => {
    value.map((item, index) => {
      initialNodes.push({
        id: `${item.id}`,
        position: {
          x: Math.floor(Math.random() * 1800),
          y: Math.floor(Math.random() * 1800),
        },
        type: "diagram_component",
        data: {
          name: item.name,
          id: `${item.id}`,
        },
      });
    });
  });

// companyDiagram.teams.map((i) => )
//       initialNodes.push({
//         id: `${item.id}`,
//         position: {
//           x: Math.floor(Math.random() * 1800),
//           y: Math.floor(Math.random() * 1800),
//         },
//         type: "diagram_component",
//         data: {
//           name: item.name,
//           id: `${item.id}`,
//         },
//       });
//     });
//   });

  companyDiagram.teams.map((item, index) => {
    initialEdges.push({
      id: `e${item.id}-${item.departmentId}`,
      source: `${item.id}`,
      target: `${item.departmentId}`,
    });
  });

  companyDiagram.departments.map((item, index) => {
    initialEdges.push({
      id: `e${item.id}-${item.componentsId}`,
      source: `${item.id}`,
      target: `${item.componentsId}`,
    });
  });

  const [nodes, setNodes, onNodesChange] = useNodesState(initialNodes);
  const [edges, setEdges, onEdgesChange] = useEdgesState(initialEdges);

  const onConnect = useCallback(
    (params) => setEdges((eds) => addEdge(params, eds)),
    [setEdges]
  );

  return (
    <div className={styles.diagram}>
      <ReactFlow
        nodes={nodes}
        edges={edges}
        onNodesChange={onNodesChange}
        onEdgesChange={onEdgesChange}
        onConnect={onConnect}
        nodeTypes={nodeTypes}
        fitView
      >
        <Controls />
        <MiniMap />
      </ReactFlow>
    </div>
  );
}

export default Diagram;
